.feed {
  width: 40%;
  margin-left: 30%;
  text-align: center;
}

.card {
  width: 100%;
  height: 20rem;
  background-color: cadetblue;
  margin-bottom: 3rem;
}

.contacts {
  background-color: cadetblue;
  width: 20%;
  margin-left: auto;
  text-align: center;
  height: 20rem;
}

@media only screen and (max-width: 1000px) {
  .feed {
    background-color: cadetblue;
    width: 100%;
    margin: 0;
  }
  .contacts {
    background-color: cadetblue;
    width: 100%;
  }
}
